import React from "react";
import { Link } from "react-router-dom";


const Showcase = () => {

    return(
        <div className="showcase">
            <div className="showcase_subtitle">
                <p></p>
            </div>
            <div className="showcase_top">
                <div className="showcase_item">
                    <Link to="coats"><img src="https://www.uniqlo.com.hk/public/image/L3/2022/l2_lineup/1125/women/w_OUTERWEAR_7.jpg"  alt="" /></Link>
                    <Link to="coats"><h1></h1></Link>
                    <Link to="coats"><p></p></Link>
                </div>
                <div className="showcase_item">
                    <Link to="blazers"><img src="https://www.uniqlo.com.hk/public/image/L3/2022/l2_lineup/1125/women/w_OUTERWEAR_5.jpg" alt=" / " /></Link>
                    <Link to="blazers"><h1> / </h1></Link>
                    <Link to="blazers"><p></p> </Link>
                </div>
                <div className="showcase_item">
                    <Link to="down"><img src="https://www.uniqlo.com.hk/public/image/L3/2022/l2_lineup/1125/women/w_OUTERWEAR_2.jpg" alt="" /></Link>
                    <Link to="down"><h1></h1></Link>
                    <Link to="down"><p></p></Link>
                </div>
            </div>
            <div className="showcase_subtitle">
                <p></p>
            </div>
            <div className="showcase_bottom">
            <div className="showcase_item">
                    <Link to="t-shirts"><img src="https://www.uniqlo.com.hk/public/image/L3/2022/l2_lineup/1125/women/w_top_8.jpg"  alt="T / " /></Link>
                    <Link to="t-shirts"><h1>T / </h1></Link>
                    <Link to="t-shirts"><p>T</p></Link>
                </div>
                <div className="showcase_item">
                    <Link to="shirts"><img src="https://www.uniqlo.com.hk/public/image/L3/2022/l2_lineup/1125/men/m_top_5.jpg" alt="" /></Link>
                    <Link to="shirts"><h1></h1></Link>
                    <Link to="shirts"><p></p> </Link>
                </div>
                <div className="showcase_item">
                    <Link to="long"><img src="https://www.uniqlo.com.hk/public/image/L3/2022/l2_lineup/1125/men/m_top_7.jpg" alt=" / 7T" /></Link>
                    <Link to="long"><h1> / 7T</h1></Link>
                    <Link to="long"><p>T</p></Link>
                </div>
            </div>
        </div>
    )
}

export default Showcase;